"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { sanityClient } from "@/lib/sanity";
import { PortableText } from "@portabletext/react";
import Footer from "@/components/footer";
import BlogHeader from "@/components/BlogHeader";
import { Linkedin, Link as LinkIcon } from "lucide-react";
import { portableTextComponents } from "@/lib/portableTextComponents";

export const dynamic = "force-dynamic";

/* ----------------------------------------
   HELPERS
---------------------------------------- */
function calculateReadTime(content = []) {
  const text = content
    .filter((block) => block._type === "block")
    .flatMap((block) => block.children?.map((child) => child.text) || [])
    .join(" ");

  const words = text.split(/\s+/).length;
  const minutes = Math.max(1, Math.ceil(words / 200));
  return `${minutes} min read`;
}

/* ----------------------------------------
   PAGE
---------------------------------------- */
export default function BlogClient({ slug }) {
  const [linkCopied, setLinkCopied] = useState(false);
  const [post, setPost] = useState(null);
  const [relatedPosts, setRelatedPosts] = useState([]);
  const [progress, setProgress] = useState(0);
  const [pageUrl, setPageUrl] = useState("");

  useEffect(() => {
    setPageUrl(window.location.href);
  }, []);

  useEffect(() => {
    const onScroll = () => {
      const total =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;
      setProgress((window.scrollY / total) * 100);
    };
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  useEffect(() => {
    if (!slug) return;

    async function fetchData() {
      const postData = await sanityClient.fetch(
        `*[_type=="post" && slug.current==$slug][0]{
          _id,
          title,
          excerpt,
          publishedAt,
          mainImage{ asset->{url} },
          categories[]->{_id,title},
          author->{ name, image{asset->{url}} },
          content
        }`,
        { slug }
      );

      setPost(postData);

      if (postData?.categories?.length) {
        const categoryIds = postData.categories.map((c) => c._id);

        const related = await sanityClient.fetch(
          `*[_type=="post" &&
            references($categoryIds) &&
            slug.current != $slug][0...3]{
              _id,
              title,
              "slug": slug.current,
              excerpt,
              mainImage{ asset->{url} },
              categories[]->{title},
              author->{ name, image{asset->{url}} }
            }`,
          { categoryIds, slug }
        );

        setRelatedPosts(related);
      }
    }

    fetchData();
  }, [slug]);

  if (!post) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        Loading article…
      </div>
    );
  }

  const readTime = calculateReadTime(post.content);

  return (
    <>
      <BlogHeader />

      <div className="fixed top-0 left-0 w-full h-1 bg-gray-200 z-50">
        <div
          className="h-full bg-blue-600 transition-all"
          style={{ width: `${progress}%` }}
        />
      </div>

      {pageUrl && (
        <div className="hidden lg:flex fixed right-6 top-1/3 flex-col gap-3 z-40">
          <button
            onClick={async () => {
              await navigator.clipboard.writeText(pageUrl);
              setLinkCopied(true);
              setTimeout(() => setLinkCopied(false), 2000);
            }}
            className={`w-10 h-10 rounded-full border flex items-center justify-center
              ${
                linkCopied
                  ? "bg-green-600 text-white"
                  : "bg-white text-gray-700"
              }`}
          >
            <LinkIcon size={18} />
          </button>

          <a
            href={`https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(
              pageUrl
            )}`}
            target="_blank"
            rel="noopener noreferrer"
            className="w-10 h-10 rounded-full border bg-white flex items-center justify-center text-[#0A66C2]"
          >
            <Linkedin size={18} />
          </a>
        </div>
      )}

      <main className="max-w-4xl mx-auto px-5 py-16 pt-20">
        <Link href="/blog" className="text-blue-600 hover:underline">
          ← Back to Blog
        </Link>

        <h1 className="text-4xl font-bold mt-6 mb-4">
          {post.title}
        </h1>

        <div className="text-sm text-gray-500 mb-8">
          {readTime}
        </div>

        {post.mainImage?.asset?.url && (
          <img
            src={post.mainImage.asset.url}
            alt={post.title}
            className="w-full rounded-3xl mb-12"
          />
        )}

        <article className="prose prose-lg max-w-none">
          <PortableText
            value={post.content}
            components={portableTextComponents}
          />
        </article>
      </main>

      <Footer />
    </>
  );
}